import { createSlice, nanoid, createAsyncThunk } from '@reduxjs/toolkit'



export const AlbumSlice = createSlice({
  name: "album",
  initialState: {
    value: 0,
    albumResults: [],
    artist: "",
    totalResults:0,
    posts: []
  },
  reducers: {
    IncreaseLimitBy4: (state) => {
      // Redux Toolkit allows us to write "mutating" logic in reducers. It
      // doesn't actually mutate the state because it uses the Immer library,
      // which detects changes to a "draft state" and produces a brand new
      // immutable state based off those changes
      state.value += 4;
    },
    storeArtist:  (state,action) => {
      state.posts.push(action.payload)
      let target = document.getElementById("outlined-basic");
      state.artist = target.value
    },
    sendArtistRequest: (state,action) => {
      state.posts.push(action.payload)
    },
    beginProcess: (state) => {
      this.storeArtist();
      console.log("Album Slice is reading ", state.artist + "in our store")
    }

  }
  }
);


// Action creators are generated for each case reducer function
export const {
  IncreaseLimitBy4,
  sendArtistRequest,
  storeArtist,
  beginProcess
} = AlbumSlice.actions;



export default AlbumSlice.reducer;